.container{
    margin: 16px;
    padding: 15px;
    background-color: #464646;
    border-radius: 10px;
    display: block;
}

.quick-view-conatiner{
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
    justify-content: center;
}

.quick-view-conatiner div{
    overflow: hidden;
    text-wrap: nowrap;
}

.quick-view-conatiner div h3{
    margin-bottom: 10px;
    text-decoration: underline;
    text-decoration-color: var(--primary-color);
    text-decoration-thickness: 3px;
    text-underline-offset: 10px;
}

.quick-view-conatiner div p{
    overflow: hidden;
    text-overflow: ellipsis;
}

.booking-form-conainter{
    display: flex;
    justify-content: center;
    max-width: 2000px;
}

.booking-form-conainter aside{
    width: 100%;
}

.bf-data{
    min-width: 60vw;
    max-width: 70vw;
}

.bf-actions,
.bf-notes{
    min-width: 28vw;
}

.bf-notes div:nth-child(3){
    align-items: center;
}

.approval-history-container,
.note-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    /* padding: 0 20px; */
    overflow: hidden;
}

.note-container:not(:first-child):has(.note-from){
    margin-top: 15px;
}

.approval-history-container .note,
.note-container .note{
    max-width: 100%;
    text-align: left;
    border-left: 2px solid var(--primary-color);
    margin-left: 20px;
    padding-left: 10px;
}

.note-container .note .approval-info{
    display: inline-flex;
    gap: 5px;
    align-items: center;
}

.note-container .note .approval-info .icon{
    font-size: 1.2em;
}

.note-container .note .approval-info p{
    font-size: .8em;
}

.container section{
    background-color: #292929;
    padding: 16px;
    border-radius: 10px;
}

.container section:not(:last-child){
    margin-bottom: 20px;
}

.container .header-information{
    background-color: #111111;
    padding: 5px;
    border-radius: 5px;
    margin-bottom: 15px;
}

.container .header-information p button{
    margin-right: 5px;
    /* transform: rotate(320deg); */
}

.container .header-information p{
    font-size: 1.5rem;
}

.container .data{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
}

.container .data:not(:nth-child(2)){
    margin-top: 20px;
}

.address-main-container,
.input-container{
    width: 100%;
}

/* Approvals */
.approver-container{
    /* background-color: rebeccapurple; */
    --_approved-color: #47d376;
    --_pending-color: #ffd900b7;
    --_rejected-color: #c91f1f;
    display: block;
    width: 100%;
    padding: 0 20px;
    margin-top: 5px;
}

.approver-container .information{
    display: flex;
    justify-content: start;
    align-items: center;
    gap: 10px;
    position: relative;
}

.approver-container .information.rejected .icon,
.approver-container .information.pending .icon,
.approver-container .information.approved .icon{
    font-size: 30px;
}

.approver-container .information.rejected .icon{
    color: var(--_rejected-color);
}

.approver-container .information.pending .icon{
    color: var(--_pending-color);
}

.approver-container .information.approved .icon{
    color: var(--_approved-color);
}

/* .approver-container .information.rejected:not(.no-after-content):after,
.approver-container .information.pending:not(.no-after-content):after,
.approver-container .information.approved:not(.no-after-content):after{
    content: '';
    position: absolute;
    height: 25px;
    left: 14px;
    top: 28px;
} */

.approver-container .information.rejected:not(.no-after-content):after{
    border: 1px solid var(--_rejected-color);
}

.approver-container .information.pending:not(.no-after-content):after{
    border: 1px solid var(--_pending-color);
}

.approver-container .information.approved:not(.no-after-content):after{
    border: 1px solid var(--_approved-color);
}


/* Model Image */
.img-viewer{
    height: auto; 
    display: block; 
    position: absolute; 
    left: 50%; 
    top: 50%; 
    transform: translate(-50%, -50%); 
    max-width: 80%;
    max-height: 90vh;
}

.download-icon,
.close-icon{ 
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 3;
    border: 1px solid #000;
    border-radius: 1px;
    cursor: pointer;
}

.download-icon{
    right: 50px;
}

/* Model Note */
.modal-note-conatiner{
    width: 50vw;
    margin: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-height: 80vh;
    padding: 30px;
    overflow: hidden;
}

.note-inputs-container{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    padding: 10px 10px;
    gap: 10px;
    max-height: 70vh;
    overflow-y: auto;
    overflow-x: hidden;
}

.note-actions-container{
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 15px;
}


/* Address Styles */
    .address-main-container{
        margin-top: 8px;
        display: flex;
        position: relative;
        border: 1px solid rgba(255, 255, 255, 0.23);
        border-radius: 4px;
    }

    .address-main-container > label {
        align-self: start;
        background-color: #292929;
        /* font-size: 1.05rem; */
        font-size: .8rem;
        padding-left: 0.5em;
        padding-right: 0.5em;
        transform: translate(0.5rem, -50%);
        position: absolute;
        color: rgba(255, 255, 255, 0.7);
        letter-spacing: .5px;
        z-index: 2;
        border-radius: 5px;
        white-space: nowrap;
        overflow: hidden;
        max-width: calc(100% - 22px);
        text-overflow: ellipsis;
    }

    .address-main-container > label.required::after {
        content: '*';
        font-family: 'Courier New', Courier, monospace, 'Franklin Gothic Medium' , 'Times New Roman', Times, serif, Arial, Helvetica, sans-serif, Georgia ;
        color: red;
    }

    .address-main-container > label span{
        /* font-size: .6rem; */
        font-size: .5rem;
        margin-left: 5px;
        font-weight: 400;
    }

    .address-container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        gap: 1.3rem;
        padding: 20px;
        margin-top: 15px;
    }

    .address-container .two-input-container{
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }

    .wrong-input{
        border-color: red !important;
        outline-color: red !important;
    }

    .address-main-container label.disabled {
        background-image: none;
        cursor: not-allowed;
    }

    .disabled {
        /* border: 1px solid #999999; */
        background-color: #cccccc;
        color: #666666;
        cursor: not-allowed;
    }



@media ( min-width: 330px ) {
    .address-main-container > label span{
        font-size: .8rem;
    }
    
    .address-container .two-input-container{
        flex-direction: row;
    }
}

@media ( min-width: 400px ) {
    .note-actions-container{
        flex-direction: row;
    }
}

@media ( min-width: 600px ) {
    .container .data{
        flex-direction: row;
    }

    .address-main-container,
    .input-container{
        width: 45%;
    }

    .approval-history-container,
    .note-container{
        padding: 0 30px;
    }
}

@media ( min-width: 800px ) {
    .img-viewer{
        max-width: 60%;
    }
}


@media ( max-width: 1200px ) {   
    .booking-form-conainter{
        /* display: block; */
        flex-wrap: wrap-reverse;
    }

    .bf-data{
        max-width: 100vw;
    }

    .modal-note-conatiner{
        width: 80vw;
    }
}