

.input-group{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
}

.input-group.fetched-data-iterator{
    /* display: none; */
    flex-direction: row;

}

.input-group:nth-child(1){
    padding-top: 8px;
}

@media ( min-width: 600px ) {
    .input-group{
        flex-direction: row;
    }

    .input{
        width: 45%;
    }
}