/* .tool-bar, */
.view-container{
    margin: 16px;
    padding: 15px;
    background-color: #464646;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.tool-bar{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #292929;
    width: 100%;
    /* margin: auto; */
    border-radius: 10PX;
    /* padding: 0 10px; */
}

.tool-bar .right,
.tool-bar .search-conatiner {
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: auto;
}

.icon{
    height: 100%;
    padding: 10px;
    display: grid;
    position: relative;
    cursor: pointer;
    z-index: 3;
    background-color: #292929;
}

.search-conatiner .input{
    background-color: #292929;
    /* position: absolute; */
    display: none;
    /* transform: translateX(-100%); */
}
    
.search-conatiner .active{
    display: block;
    animation-name: searchbar-animation-in;
    animation-duration: 0.1s;
    animation-timing-function: ease-in;
    animation-delay: 0s;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: forwards;    
}

.drawer > div{
    display: flex !important;
    flex-direction: column;
    /* align-items: center; */
    justify-content: space-between !important;
    padding: 10px 0;
}

.drawer .filter-actions{
    display: flex;
    justify-content: space-evenly;
    padding: 10px 0;
}
.drawer .filter-actions button{
    width: 35%;
}

.filters-list li:first-child{
    justify-content: center;
    letter-spacing: 2.5px;
}

.filters-list .filter{ 
    display: block;
    width: 100%;
}

@keyframes searchbar-animation-in {
    from{
        transform: translateX(-100%);
    }
    to{
        transform: translateX(0%);
    }
}
